.nav-outer {
  position: sticky;
  top: 0px;
  z-index: 99;
  display: block;
  height: 64px;
  margin: auto;
  padding: 20px;
  align-self: auto;
  order: 0;
  flex: 0 auto;
  border: 1px none #000;
  background-color: #fff;
  box-shadow: none;
  -o-object-fit: fill;
  object-fit: fill;
  transition: all .3s ease-out;
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.08); }
  .nav-outer .nav {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: grid;
    max-width: 900px;
    width: 100%;
    padding: 0px;
    justify-content: center;
    align-items: center;
    align-content: start;
    flex: 0 auto;
    grid-auto-flow: column dense;
    grid-auto-columns: max-content;
    grid-column-gap: 16px;
    grid-row-gap: 16px;
    grid-template-columns: 1fr max-content;
    grid-template-rows: auto; }
  .nav-outer .logo {
    justify-self: start;
    height: 24px; }
    .nav-outer .logo img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .nav-outer .menu-list {
    display: flex;
    flex-wrap: nowrap; }
  .nav-outer li {
    margin-right: 16px;
    color: #323232;
    font-size: 16px;
    cursor: pointer; }
    .nav-outer li .menu-list-items {
      color: #9a9a9a; }
      .nav-outer li .menu-list-items.nav-list-active {
        position: relative;
        color: #323232; }
        .nav-outer li .menu-list-items.nav-list-active:after {
          content: "";
          display: block;
          position: absolute;
          bottom: -4px;
          left: 50%;
          transform: translateX(-50%);
          width: 20px;
          height: 2px;
          background-color: #00b4d8;
          border-radius: 4px; }
  .nav-outer .select_language {
    display: none;
    margin-right: 16px; }
  .nav-outer .nav-list-open {
    grid-template-columns: 1fr; }

.menu-buttom {
  display: none;
  position: absolute;
  top: -20px;
  right: -16px;
  cursor: pointer; }

.ham {
  transition: transform 400ms;
  user-select: none; }

.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000;
  stroke-width: 5.5;
  stroke-linecap: round; }

.ham8 {
  display: block; }
  .ham8 .top {
    stroke-dasharray: 40 160; }
  .ham8 .middle {
    stroke-dasharray: 40 142;
    transform-origin: 50%;
    transition: transform 400ms; }
  .ham8 .bottom {
    stroke-dasharray: 40 85;
    transform-origin: 50%;
    transition: transform 400ms, stroke-dashoffset 400ms; }
  .ham8.menu-open .top {
    stroke-dashoffset: -64px; }
  .ham8.menu-open .middle {
    transform: rotate(90deg); }
  .ham8.menu-open .bottom {
    stroke-dashoffset: -64px; }

.hamRotate.menu-open {
  transform: rotate(45deg); }

@media screen and (min-width: 768px) {
  .nav-outer li .menu-list-items:hover {
    color: #323232; } }

@media screen and (max-width: 576px) {
  .nav-outer {
    height: unset; }
    .nav-outer .nav {
      grid-auto-flow: unset;
      grid-template-columns: repeat(1, 1fr); }
    .nav-outer .menu-list {
      display: none; }
    .nav-outer .nav-list-open.menu-list {
      display: flex;
      flex-direction: column; }
      .nav-outer .nav-list-open.menu-list li {
        margin: 0;
        padding: 16px 0; }
    .nav-outer .menu-buttom {
      display: block; } }

